import React from 'react';
import "./footer.css";
import { FiGithub, FiLinkedin } from 'react-icons/fi';

const Footer = () => {
  return (
    <footer className="footer">
        <div className="footer__container container">
            <h1 style={{fontSize:"30px"}} className="footer__title">Shivani Neharkar</h1>
            <ul className="footer__list">
                <li>
                    <a href="#about" className="footer__link">About</a>
                </li>
                <li>
                    <a href="#portfolio" className="footer__link">Projects</a>
                </li>
                <li>
                    <a href="#qualification" className="footer__link">Qualification</a>
                </li>
            </ul>
            <div className="footer__social">
                {/* <a href="https://twitter.com/cyphrsylph" className="home__social-icon" target="_blank" rel="noreferrer" style={{color:'white'}}>
                    <FiTwitter />
                </a> */}
                <a href="https://github.com/ShivaniNeharkar" className="home__social-icon" target="_blank" rel="noreferrer" style={{color:'white'}}>
                    <FiGithub />
                </a>
                <a href="https://in.linkedin.com/in/shivani-neharkar-b762b719b" className="home__social-icon" target="_blank" rel="noreferrer" style={{color:'white'}}>
                    <FiLinkedin />
                </a>    
            </div>
            <span className="footer__copy"></span>
        </div>
    </footer>
  );
}

export default Footer;