import React from 'react';
import { HiOutlineArrowSmRight } from "react-icons/hi";

const ProjectItems = ({item}) => {
  return (
        <div className="project__card" key={item.id}>
            <img className="project__img" src={item.image} alt="" />
            <h3 className="project__title">{item.title}</h3>
            <div style={{display:"inline-flex"}}>
                {item.demo && <a href={item.demo} rel="noreferrer" target="_blank" className='project__button' style={{padding:"13px"}}>
                    Demo <HiOutlineArrowSmRight style={{color:'blue'}}className="project__button-icon" />
                </a>}
                <a href={item.code} rel="noreferrer" target="_blank" className='project__button' style={{padding:"13px"}}>
                    Code <HiOutlineArrowSmRight style={{color:'blue'}}className="project__button-icon" />
                </a>
            </div>
        </div>
    );
}

export default ProjectItems;