import React from "react";
import "../projects/projects.css";
import Paper1IMG from "../../assets/Paper1.jpg";
import Paper2IMG from "../../assets/Paper2.jpg";

import Paper2PDF from "../../assets/CA_PAPER.docx.pdf";
import {
    HiOutlineArrowSmRight
  } from "react-icons/hi";
const Paper = () => {
  return (
    <React.Fragment>
        <div className="project__info__modal grid">
      <div className="project__card_modal" >
            <img className="project__img" src={Paper1IMG} alt="Paper1 Image" />
            <h3 className="project__title">IRJET Published, VehiChain: Blockchain-based Service for Storing Vehicle Records.</h3>
            <div style={{display:"inline-flex"}}>
                <a href="https://www.irjet.net/archives/V7/i12/IRJET-V7I12188.pdf" rel="noreferrer" target="_blank" className='project__button' style={{padding:"13px"}}>
                    View Paper <HiOutlineArrowSmRight style={{color:'blue'}}className="project__button-icon" />
                </a>
            </div>
        </div>
        <div className="project__card_modal">
            <img className="project__img" src={Paper2IMG} alt="Paper2 Image" />
            <h3 className="project__title">Comparative Performance Evaluation of Tensor Processing Unit (TPU) and Graphics
Processing Unit (GPU) across a range of Deep Learning Models</h3>
            <div style={{display:"inline-flex"}}>
                <a href={Paper2PDF} rel="noreferrer" target="_blank" className='project__button' style={{padding:"13px"}}>
                    View Paper <HiOutlineArrowSmRight style={{color:'blue'}}className="project__button-icon" />
                </a>
            </div>
        </div>
        </div>
    </React.Fragment>
  );
};
export default Paper;

