import React from "react";
import {FiGithub, FiLinkedin} from "react-icons/fi";

const Social = () => {
    return (
        <div className="home__social">
            <a href="https://github.com/ShivaniNeharkar" className="home__social-icon"  rel="noreferrer" target="_blank">
                <FiGithub />
            </a>
            <a href="https://in.linkedin.com/in/shivani-neharkar-b762b719b" className="home__social-icon" rel="noreferrer" target="_blank">
                <FiLinkedin />
            </a>
        </div> 
    ); 
}

export default Social;