import React,{useRef,useState}from 'react';
import "./contact.css";
import { HiOutlineMail, HiOutlineArrowSmRight } from "react-icons/hi"
import {IoMdDoneAll}from "react-icons/io"
import emailjs from '@emailjs/browser';

const Contact = () => {
    const [sentMail,setSentMail]= useState(false);
    const form=useRef();
    const sendEmail = (e) => {
        e.preventDefault();
    
        emailjs.sendForm('service_p0dyihg', 'template_t62w5vf', form.current, 'H-a5XWEM20dF0RyYk')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          setSentMail(true);
          e.target.reset();
      };
      
  
    return (
    <section className="contact section" id="contact">
        <h2 className="section__title">Let's Connect</h2>
        <span className="section__subtitle">Contact Me</span>

        <div className="contact__container container grid">
            <div className="contact__content">
                <h3 className="contact__title">Get in Touch</h3>
                
                <div className="contact__info">
                    <div className="contact__card">
                        <HiOutlineMail className="contact__card-icon" />
                        
                        <h3 className="contact__card-title">Email</h3>
                        <span className="contact__card-data">shivanineharkar88@gmail.com</span>
                        
                        <a href="mailto:shivanineharkar@gmail.com" className="contact__button">
                            Write{" "} 
                            <HiOutlineArrowSmRight className="contact__button-icon" />
                        </a>
                    </div>
                </div>
            </div>

            <div className="contact__content">
                <h3 className="contact__title">Type Message here</h3>

                <form ref={form}onSubmit={sendEmail} className="contact__form">
                    <div className="contact__form-div">
                        <label className="contact__form-tag">Name</label>
                        <input type="text" name="name" className="contact__form-input" placeholder="Type your name" />
                    </div>

                    <div className="contact__form-div">
                        <label className="contact__form-tag">Email</label>
                        <input id="from" type="email" name="email" className="contact__form-input" placeholder="Type your email" />
                    </div>

                    <div className="contact__form-div contact__form-area">
                        <label className="contact__form-tag">Message</label>
                        <textarea name="message" cols="30" rows="10" className="contact__form-input" placeholder="Message box"></textarea>
                    </div>

                    <button href="#contact" className="button button--flex" >
                        {sentMail?"Sent":"Send Message"}
                        {sentMail && <IoMdDoneAll  style={{marginLeft:"5px", backgroundColor:"#39b376"}}className="contact__button-icon" />}
                    </button>
                </form>
            </div>
        </div>
    </section>
  );
}

export default Contact;