import React,{useState} from "react";
import {
  HiOutlineCode,
  HiOutlineTerminal,
  HiOutlineSparkles,
} from "react-icons/hi";
import Modal from "../modal/Modal";
import Certificates from "../about/certificates";
import Paper from"./Paper";
const Info = () => {
  const [showModal, setShowModal]= useState(false);
  const [showPaper, setPaperModal]= useState(false);

  const modalHandler=()=>{
    setShowModal(true);
  }
  const modalCancelHandler=()=>{
    setShowModal(false);
  }
  const showPaperHandler=()=>{
    setPaperModal(true);
  }
  const showPaperCancelHandler=()=>{
    setPaperModal(false);
  }
  return (
    
    <React.Fragment>
    
    <Modal
        show={showModal}
        onCancel={modalCancelHandler}
        header="Click to view Certificate"
        footerClass="place-item__modal-actions"
        footer={
          <React.Fragment>
            <button className="button button--flex" onClick={modalCancelHandler}>CANCEL</button>
          </React.Fragment>
        }
      >
        <><Certificates/></>
      
      </Modal>
      <Modal
        show={showPaper}
        onCancel={showPaperCancelHandler}
        header="Click to view Papers"
        footerClass="place-item__modal-actions"
        footer={
          <React.Fragment>
            <button className="button button--flex" onClick={showPaperCancelHandler}>CANCEL</button>
          </React.Fragment>
        }
      >
        <><Paper/></>
      
      </Modal>



    <div className="about__info grid">
      <div className="about__box">
        <HiOutlineCode className="about__icon" />
        <h3 className="about__title">Coding Experience</h3>
        <br/>
        <span className="about__subtitle">6 Years</span>
      </div>
      <div className="about__box">
        <HiOutlineTerminal className="about__icon" />
        <h3 className="about__title">Certifications</h3>
        <br/>
        <button style={{backgroundColor:"white", color:"#7643DF"}} onClick={modalHandler} className="about__subtitle">
          <u>View Certificates</u>
        </button>
      </div>
      <div className="about__box">
        <HiOutlineSparkles className="about__icon" />
        <h3 className="about__title">Paper Published</h3>
        <br/>
        <button style={{backgroundColor:"white", color:"#7643DF"}} onClick={showPaperHandler} className="about__subtitle">
          <u>View Papers</u>
        </button>

        {/* <a href="https://www.irjet.net/archives/V7/i12/IRJET-V7I12188.pdf" target="_blank"  rel="noreferrer" className="about__subtitle">
          <br/>
          <u style={{color:"#7643DF"}}>View Paper</u>
        </a> */}
      </div>
    </div>
    </React.Fragment>
  );
};

export default Info;
